.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 10%;
    background: rgb(0,0,0);
    background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(18,18,198,1) 83%, rgba(255,0,241,1) 100%);
    > img {
        height: 100%;
    }
    > div {
        display: flex;
        width: 40%;
        justify-content: space-evenly;
        > div {
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            width: 60%;
            > div {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 40%;
                > button {
                    width: 80%;
                    text-decoration: none;
                    border: none;
                    background: radial-gradient(circle at 100% 100%, #771ce8 0, #771ce8 3px, transparent 3px) 0% 0%/8px 8px no-repeat,
                    radial-gradient(circle at 0 100%, #771ce8 0, #771ce8 3px, transparent 3px) 100% 0%/8px 8px no-repeat,
                    radial-gradient(circle at 100% 0, #771ce8 0, #771ce8 3px, transparent 3px) 0% 100%/8px 8px no-repeat,
                    radial-gradient(circle at 0 0, #771ce8 0, #771ce8 3px, transparent 3px) 100% 100%/8px 8px no-repeat,
                    linear-gradient(#771ce8, #771ce8) 50% 50%/calc(100% - 10px) calc(100% - 16px) no-repeat,
                    linear-gradient(#771ce8, #771ce8) 50% 50%/calc(100% - 16px) calc(100% - 10px) no-repeat,
                    conic-gradient(from 60deg, #ff0000 0%, #ffff00 16.67%, #00ff00 33.33%, #00ffff 50%, #0000ff 66.67%, #ff00ff 83.33%, #ff0000 100%);
                    border-radius: 8px;
                    padding: 9px;
                    box-sizing: border-box;
                    color: orange;
                }
                > button:hover {
                    width: 100%;
                    cursor: pointer;
                    transition: 0.3s;
                }
            }
        }
    }
}