@import url('https://fonts.googleapis.com/css2?family=Sono&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap');
.myProfile {
  display: flex;
  flex-direction: row;
  height: 100%;
  overflow: scroll;
  overflow-x: hidden;
  > :nth-child(1) {
    display: flex;
    flex-direction: column;
    padding: 5%;
    width: 50%;
    height: 100%;
    color: white;
    font-family: 'Inter', sans-serif;

    > :nth-child(1) {
      display: flex;
      align-items: flex-start;
      overflow: hidden;
      height: 45%;
      width: 100%;
      > img {
        position: relative;
        width: 80%;
      }
    }
    > :nth-child(2) {
      display: flex;
      flex-direction: row;
      align-items: center;
      > div {
        width: 1em;
        height: 1em;
        border-radius: 100%;
        background-color: greenyellow;
        margin-right: 5%;
      }
      > p {
        color: #d7c6c6;
        font-family: 'Sono', sans-serif;
      }
    }
    > :nth-child(3) {
      font-family: 'Inter', sans-serif;
    }
    > :nth-child(4) {
    }
    > :nth-child(5) {
    }
    > :nth-child(6) {
    }
    > :nth-child(7) {
    }
    > :nth-child(8) {
    }
  }
  > :nth-child(2) {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 50%;
    height: 100%;
    > :nth-child(1) {
      display: flex;
      flex-direction: column;
      width: 100%;
      > textarea {
        margin: 2% 0;
        width: 100%;
        height: 10vh;
      }
      > :nth-child(2) {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        > button, > input {
            font-size: 14px;
            color: #fff;
            background: linear-gradient(90deg, #0066CC 0%, #c500cc 100%);
            padding: 10px 30px;
            border: 2px solid #0066cc;
            box-shadow: rgb(0, 0, 0) 0px 0px 0px 0px;
            border-radius: 50px;
            transition : 236ms;
            transform: translateY(0);
            display: flex;
            flex-direction: row;
            align-items: center;
            cursor: pointer;
        }
        > input::after {
            border: none;
            background-color: #54b6cd;
        }
        > :hover{
          transition : 236ms;
          padding: 10px 50px;
          transform : translateY(-0px);
          background: linear-gradient(90deg, #0066CC 0%, #c500cc 100%);
          color: #e0e0e0;
          border: solid 2px #0066cc;
        }
      }
    }
    > :last-child {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      width: 100%;
      flex-direction: column;
    }
  }

}