@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap');

.LoginForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-image: url("./../../../src/assets/img/backForm.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position-x: center;
  > section:has(h1) {

  }
  > section:has(form) {
    height: 50%;
    width: 46vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgba(15, 3, 178, 0.23);
    > p {
      margin: 0;
    }
    > form {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 50%;
      height: 100%;
      justify-content: space-between;
      padding: 5%;
      > input {
        width: 80%;
        height: 2vh;
        border-left: none;
        border-top: none;
        border-right: none;
        border-bottom: 0;

      }
      >input:focus {
        outline: none;
        -webkit-box-shadow: 0px 5px 38px 20px #0b1f7a;
        box-shadow: 0px 5px 38px 20px #0b1f7a;
        transition: linear 0.2s;
      }
      > label {
        font-family: 'Oswald', sans-serif;
      }
      > button {
        font-family: 'Oswald', sans-serif;
        width: 50%;
        text-decoration: none;
        border: none;
        background: radial-gradient(circle at 100% 100%, #771ce8 0, #771ce8 3px, transparent 3px) 0% 0%/8px 8px no-repeat,
        radial-gradient(circle at 0 100%, #771ce8 0, #771ce8 3px, transparent 3px) 100% 0%/8px 8px no-repeat,
        radial-gradient(circle at 100% 0, #771ce8 0, #771ce8 3px, transparent 3px) 0% 100%/8px 8px no-repeat,
        radial-gradient(circle at 0 0, #771ce8 0, #771ce8 3px, transparent 3px) 100% 100%/8px 8px no-repeat,
        linear-gradient(#771ce8, #771ce8) 50% 50%/calc(100% - 10px) calc(100% - 16px) no-repeat,
        linear-gradient(#771ce8, #771ce8) 50% 50%/calc(100% - 16px) calc(100% - 10px) no-repeat,
        conic-gradient(from 60deg, #ff0000 0%, #ffff00 16.67%, #00ff00 33.33%, #00ffff 50%, #0000ff 66.67%, #ff00ff 83.33%, #ff0000 100%);
        border-radius: 8px;
        padding: 9px;
        box-sizing: border-box;
        color: orange;
      }
      > button:hover {
        width: 60%;
        cursor: pointer;
        transition: 0.3s;
      }
      > p {
        text-align: center;
        background-color: rgba(40, 63, 175, 0.58);
      }
    }

  }
}

.errMsg {
  display: block;
  background-color: red;
}

.offScreen {
  display: none;
}